import styled from 'styled-components';

export const Header = styled.div`
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 80%;
  margin: 0 auto;
`;

export const Result = styled.span`
  font-size: 25px;
  cursor: pointer;
`;



